import { forwardRef, Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

const starIcons = {
  outline: 'outline',
  full: 'full',
  half: 'half'
};

@Component({
  selector: 'ngx-input-star-rating',
  template: `<div class="input-rating" (mouseleave)="mouseLeave()">
  <button type="button" [disabled]="disabled" *ngFor="let btn of buttons; let i = index" class="input-rating__btn" (click)="rate(i)" [attr.aria-label]="'rate ' + (i + 1) + ' of ' + buttons.length"
    (mouseenter)="mouseEnter(i)">
    <span *ngIf="btn.active || btn.icon === starIcons.full" class="full">
      <ng-container *ngTemplateOutlet="star"></ng-container>
    </span>
    <ng-container *ngIf="!btn.active">
      <span *ngIf="btn.icon === starIcons.half" class="half">
        <ng-container *ngTemplateOutlet="star"></ng-container>
        <svg height="25" width="10" class="star-half">
          <polygon points="9.9, 1.1, 3.3, 21.78, 19.8, 8.58, 0, 8.58, 16.5, 21.78" style="fill-rule:nonzero;"/>
        </svg>
      </span>
      <span *ngIf="btn.icon !== starIcons.full && btn.icon !== starIcons.half" class="empty">
        <ng-container *ngTemplateOutlet="star"></ng-container>
      </span>
    </ng-container>
  </button>
</div>

<ng-template #star>
  <svg height="25" width="23" class="star">
    <polygon points="9.9, 1.1, 3.3, 21.78, 19.8, 8.58, 0, 8.58, 16.5, 21.78" style="fill-rule:nonzero;"/>
  </svg>
</ng-template>`,
  styles: [`*, *::before, *::after{
  -webkit-box-sizing:border-box;
          box-sizing:border-box; }

:host{
  display:inline-block; }

.star{
  fill:#ccc; }

.full .star{
  fill:gold; }

.half .star-half{
  fill:gold; }

button{
  background-color:transparent;
  border:0;
  cursor:pointer;
  position:relative;
  width:30px;
  height:25px; }
  button svg{
    position:absolute;
    top:0;
    left:5px; }
`],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => NgxInputStarRatingComponent),
      multi: true
    }
  ]
})
export class NgxInputStarRatingComponent implements ControlValueAccessor, OnInit, OnChanges {
  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(val);
    this.valueUpdate(this._value);
    this.onTouched();
  }

  @Input('value') _value = 0;
  @Input() count = 5;
  @Input() disabled = false;

  readonly starIcons = starIcons;

  buttons: { active: boolean; icon: string }[] = [];

  // tslint:disable-next-line:no-empty
  onChange = (_value: number) => {};

  // tslint:disable-next-line:no-empty
  onTouched = () => {};

  registerOnChange(fn: (value: number) => void) {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void) {
    this.onTouched = fn;
  }

  writeValue(value: number) {
    if (value !== this.value) {
      this.value = value;
    }
  }

  ngOnInit() {
    for (let i = 0; i < this.count; i++) {
      this.buttons.push({ active: false, icon: starIcons.outline });
    }

    this.valueUpdate(this.value);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.value && changes.value.currentValue) {
      this.valueUpdate(changes.value.currentValue);
    }
  }

  rate(index: number) {
    this.buttons.forEach((btn, i) => {
      if (i <= index) {
        btn.icon = starIcons.full;
      } else {
        btn.icon = starIcons.outline;
      }
    });

    const total = this.buttons.reduce((prev, next) => {
      if (next.icon === starIcons.full) {
        return prev + 1;
      } else {
        return prev;
      }
    }, 0);

    this.value = total;
  }

  mouseEnter(index: number) {
    this.buttons.forEach((btn, i) => {
      if (i <= index) {
        btn.active = true;
      } else {
        btn.active = false;
      }
    });
  }

  mouseLeave() {
    this.buttons.forEach(i => (i.active = false));
  }

  private valueUpdate(value: number) {
    this.buttons.forEach((btn, i) => {
      const starValue = i + 1;

      if (value >= starValue) {
        btn.icon = starIcons.full;
      }

      if (value >= starValue - 0.5 && value < starValue) {
        btn.icon = starIcons.half;
      }
    });
  }
}
